import { BannerEntity } from '@nequi/features/closet/core/';
import { BaseMapper } from '@nequi/features/commons';

import { BannerStatus } from '../constants/banner-status';
import { BannerViewEntity } from '../entities/banner-view-entity';
import { HomeConfig } from '../pages/home/home.config';

const OLD_APP_DEFAULT_IMAGE_URL = '../images/banner_store.svg';

export class GetBannerListMapper extends BaseMapper<BannerViewEntity> {
  public from(banner: BannerEntity): BannerViewEntity {
    return {
      deepLink: banner.deepLink,
      urlImage:
        banner.urlImage === OLD_APP_DEFAULT_IMAGE_URL
          ? HomeConfig.defaultBanner[0].urlImage
          : banner.urlImage,
      status: BannerStatus.LOADING,
    };
  }
}
