import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { ClosetConfig } from '../config/closet.config';
import { HomePage } from './pages/home/home.page';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(I18nModuleResolver.setup(routes, [ClosetConfig.i18n.moduleKey]))],
  exports: [RouterModule],
})
export class ClosetRoutingModule {}
