import { Component, Input, Renderer2 } from '@angular/core';
import { ToolViewEntity } from '@nequi/features/closet/presentation';
import { SettingInteractor } from '@nequi/features/settings/core';
import { BottomSheetController, ToastService } from '@nequi/ui';

@Component({
  selector: 'app-tool-list-bottom-sheet',
  templateUrl: './tool-list.organism.html',
  styleUrls: ['./tool-list.organism.scss'],
})
export class ToolListBottomSheetOrganism {
  @Input() public title: string;
  @Input() public items: Array<ToolViewEntity>;
  @Input() public successMessage: string;
  @Input() public toolsIndex: any;

  constructor(
    public bottomSheetController: BottomSheetController,
    private renderer: Renderer2,
    private toastService: ToastService,
    private settingInteractor: SettingInteractor
  ) {}

  public close(update: boolean) {
    this.bottomSheetController.dismiss(update);
  }

  public async changeTool(event: TouchEvent, tool: ToolViewEntity) {
    if (!tool.moveOptions.canMove) {
      await this.toastService.showError(tool.moveOptions.closetCanNotMoveMessage);

      return;
    }

    this.setCssClassName(event);
    const backpackToolIndex =
      this.toolsIndex.backpack !== undefined ? this.toolsIndex.backpack : tool.index;
    const closetToolIndex =
      this.toolsIndex.closet !== undefined ? this.toolsIndex.closet : tool.index;

    this.settingInteractor.updateToolList(backpackToolIndex, closetToolIndex);

    this.close(true);
    const message = this.successMessage + tool.title;

    await this.toastService.showSuccess(message);
  }

  private setCssClassName(event: TouchEvent) {
    const oldSelected = document.querySelector('.tool-list__card-selected') as HTMLStyleElement;

    if (oldSelected) {
      this.renderer.removeClass(oldSelected, 'tool-list__card-selected');
    }

    this.renderer.addClass(event.currentTarget, 'tool-list__card-selected');
  }
}
