import { Component, OnInit } from '@angular/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { ClosetInteractor } from '@nequi/features/closet/core';
import { AnalyticsInteractor, LoggerInteractor } from '@nequi/features/commons/core';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { SettingInteractor, ToolEntity } from '@nequi/features/settings/core';
import { ToolListBottomSheetService } from '@nequi/features/settings/presentation';
import { ResourceId } from '@nequi/features/style-manager/core';
import { FullscreenModalService, ToastService } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { BannerStatus } from '../../constants/banner-status';
import { ToolType } from '../../constants/tool-types';
import { BannerViewEntity } from '../../entities/banner-view-entity';
import { ToolViewEntity } from '../../entities/tool-view.entity';
import { GetBannerListMapper } from '../../mappers/get-banner-list.mapper';
import { HomeConfig } from './home.config';

@Component({
  selector: 'app-closet',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  public isShowBusy: boolean;
  public loadingError: string;
  public hasErrorLoading = false;
  public tools: Array<ToolEntity>;
  public bannerList: Array<BannerViewEntity>;
  public isBannerVisible = true;
  public slideOpts = {
    speed: 1500,
    autoplay: true,
  };

  public resourceId: string;
  public showSliderSkeleton = true;

  constructor(
    private fullscreenModalService: FullscreenModalService,
    private translateProvider: TranslateProvider,
    private applicationDataInteractor: ApplicationDataInteractor,
    private settingInteractor: SettingInteractor,
    private toastService: ToastService,
    private closetInteractor: ClosetInteractor,
    private navigatorProvider: NavigatorProvider,
    private loggerInteractor: LoggerInteractor,
    private toolListBottomSheetService: ToolListBottomSheetService,
    private analyticsInteractor: AnalyticsInteractor,
    private moduleConfigProvider: ModuleConfigProvider
  ) {
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    this.getBannerData();
  }

  public get bannerStatus() {
    return BannerStatus;
  }

  public async ngOnInit() {
    this.resourceId = ResourceId.CLOSET;
    await this.setStoreTools();
  }

  public async ionViewWillEnter() {
    this.isBannerVisible = true;
    await this.validateShowCoachmark();
  }

  public async setStoreTools() {
    try {
      this.isShowBusy = true;
      this.loadingError = '';
      this.tools = await this.settingInteractor.getClosetToolList();
      this.isShowBusy = false;
    } catch (error) {
      this.loggerInteractor.error(error);
      this.loadingError = error.message;
      this.hasErrorLoading = true;
    }
  }

  public async openBottomSheetToolList(event: TouchEvent, index: number, tool: ToolEntity) {
    event.preventDefault();

    if (!tool.moveOptions.canMove) {
      await this.toastService.showError(tool.moveOptions.closetCanNotMoveMessage);

      return;
    }

    const options = this.translateProvider.get(HomeConfig.changeToolMessages);

    const items = await this.settingInteractor.getBackpackToolList();

    const { data } = await this.toolListBottomSheetService.show({
      title: createFormat(options['bottom-sheet'].title, [tool.title]),
      items: items.map(
        (item: ToolEntity, count): ToolViewEntity => {
          return {
            ...item,
            index: count,
            type: ToolType.BACKPACK,
          };
        }
      ),
      successMessage: createFormat(options.success, [tool.title]),
      toolsIndex: { closet: index },
    });

    if (data === true) {
      this.setStoreTools();
    }
  }

  public onIonImgDidLoad(bannerIndex: number) {
    this.bannerList[bannerIndex].status = BannerStatus.LOADED;
    this.showSliderSkeleton = false;
  }

  public onClick(banner: BannerViewEntity) {
    if (!banner.deepLink) {
      return;
    }

    openUrl(banner.deepLink);
  }

  public cardClick(tool: ToolEntity) {
    if (!tool.url) {
      return;
    }

    if (tool.analytics) {
      tool.analytics.forEach((event) => {
        this.analyticsInteractor.trackEvent(event);
      });
    }

    this.navigatorProvider.navigateForward([tool.url], {
      queryParams: tool.params,
      state: tool,
    });
  }

  private async openCoachmark() {
    const coachmark = this.translateProvider.get(HomeConfig.coachmarkMessage);

    this.fullscreenModalService.show({
      id: HomeConfig.coachmarkTools.id,
      imgPath: HomeConfig.coachmarkTools.imgPath,
      cssClass: HomeConfig.coachmarkTools.cssClass,
      content: coachmark.message,
      fullPageDismiss: true,
    });
  }

  private async validateShowCoachmark() {
    let counter = await this.applicationDataInteractor.findValue<number>(
      this.moduleConfigProvider.applicationData.coachmarkToolViewedCounter,
      1
    );

    if (counter > HomeConfig.maxViewedCounter) {
      return;
    }

    if (counter === HomeConfig.maxViewedCounter) {
      await this.openCoachmark();
    }

    counter++;

    await this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.coachmarkToolViewedCounter,
      counter
    );
  }

  private async getBannerData() {
    try {
      const bannerList = await this.closetInteractor.getBannerList();

      this.bannerList = new GetBannerListMapper().fromList(bannerList);
    } catch (e) {
      this.bannerList = HomeConfig.defaultBanner;
    }
  }
}
