import { Injectable } from '@angular/core';
import { ToolViewEntity } from '@nequi/features/closet/presentation';
import { BaseBottomSheetService, BottomSheetController } from '@nequi/ui';

import {
  ToolListBottomSheetOrganism
} from '../organisms/bottom-sheets/tools-list/tool-list.organism';

interface Parameters {
  title: string;
  items: Array<ToolViewEntity>;
  successMessage: string;
  toolsIndex?: {
    backpack?: number;
    closet?: number;
  };
}

@Injectable()
export class ToolListBottomSheetService
  implements BaseBottomSheetService<Partial<Parameters>, boolean> {
  constructor(private bottomSheetController: BottomSheetController) {}

  public async show({ title, items, successMessage, toolsIndex }: Parameters) {
    const bottomSheet = this.bottomSheetController.create({
      isAutoHeight: true,
      component: ToolListBottomSheetOrganism,
      componentProps: { title, items, successMessage, toolsIndex },
    });

    await bottomSheet.open();

    return bottomSheet.onDidDismiss();
  }
}
