import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { ClosetInteractor, ClosetRepository } from '@nequi/features/closet/core';
import { ModuleConfigProvider } from '@nequi/features/commons/providers';
import { ToolListBottomSheetService } from '@nequi/features/settings/presentation';
import { FeaturesStyleManagerModule } from '@nequi/features/style-manager/presentation';
import { NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { ClosetConfig } from '../config/closet.config';
import { ClosetRoutingModule } from './closet-routing.module';
import { HomePage } from './pages/home/home.page';

@NgModule({
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ClosetRoutingModule,
    NequiUiModule,
    TranslateModule,
    FeaturesStyleManagerModule,
  ],
  declarations: [HomePage],
  providers: [
    {
      provide: ClosetInteractor,
      deps: [ClosetRepository],
      useFactory: (repository: ClosetRepository) => new ClosetInteractor(repository),
    },
    ToolListBottomSheetService,
    {
      provide: ModuleConfigProvider,
      useValue: ClosetConfig,
    },
  ],
})
export class FeaturesClosetModule {}
